<template>
  <div class="home-index">
    <el-card class="box-card" shadow="hover">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/home">首页</el-breadcrumb-item>
          <el-breadcrumb-item>内容管理</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-alert type="info" :closable="false" effect="dark" center>
        根据条件总共筛选到{{ totalCount }}条数据
      </el-alert>
      <div slot="header" class="clearfix">
        <el-pagination
          background
          @current-change="onCurrentChange"
          layout="prev, pager, next"
          :page-size="pageSize"
          :disabled="loading"
          :current-page.sync="index"
          :total="totalCount"
        >
        </el-pagination>
      </div>
      <el-table :data="articles" style="width: 100%" stripe v-loading="loading">
        <el-table-column label="封面">
          <template slot-scope="scope">
            <el-image
              class="el_table_img"
              :src="scope.row.cover.images[0]"
              fit="cover"
              lazy
            >
            </el-image>
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="标题"/>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-tag :type="articleStatus[scope.row.status].type">
              {{ articleStatus[scope.row.status].text }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="pubdate" label="发布时间"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="$router.push('/publish?id=' + scope.row.id)"
              circle
              icon="el-icon-edit"
            >
            </el-button>
            <el-button
              size="mini"
              circle
              icon="el-icon-delete"
              @click="onDeleteArtice(scope.row.id)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import { getArticles, deleteArticle } from '@/api/article'
export default {
  name: 'HomeIndex',
  components: {},
  props: {},
  data () {
    return {
      articleStatus: [
        { text: '草稿', type: 'warnng' },
        { text: '待审核', type: '' },
        { text: '审核通过', type: 'success' },
        { text: '审核失败', type: 'danger' },
        { text: '已删除', type: 'info' }
      ],
      articles: [],
      totalCount: 0,
      pageSize: 20,
      loading: true,
      index: 1
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticles(1)
  },
  mounted () {},
  methods: {
    async loadArticles (page) {
      this.loading = true
      const res = await getArticles({
        page: page,
        per_page: this.pageSize,
        status: 2
      })
      const { results, total_count: totalCount } = res.data.data
      this.articles = results
      this.totalCount = totalCount
      this.loading = false
    },
    onCurrentChange (page) {
      this.loadArticles(page)
    },
    onDeleteArtice (articleId) {
      this.$confirm('是否删除?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          deleteArticle(articleId).then((res) => {
            this.loadArticles(this.index)
          })
        }).catch(() => {})
    }
  }
}
</script>

<style scoped lang="less">
.el-pagination {
  margin-left: 50%;
}
.el-table .warning-row {
  background: oldlace;
}
.el-table .success-row {
  background: #f0f9eb;
}
.el_table_img {
  width: 100px;
  height: 100px;
}
</style>
